.jumbotron {
  padding-top: 3rem;
  padding-bottom: 3rem;
  margin-bottom: 0;
  min-height: 300px;
}
@media (orientation: portrait) {
  .jumbotron {
    padding-top: 1rem;
    padding-bottom: 1rem;
    margin-bottom: 0;
    min-height: 200px;
  }
}

.text-outlined {
  color: black;
  text-shadow:
    -1px -1px 0 white,  
    1px -1px 0 white,
    -1px 1px 0 white,
    1px 1px 0 white;
}

.jumbotron-heading {
  font-size: 3rem;
  font-weight: 700;
}
.jumbotron-text {
  font-size: 1.2rem;
  max-width: 30rem;
}
@media (orientation: portrait) {
  .jumbotron-heading {
    font-size: 2rem;
    font-weight: 700;
  }
  .jumbotron-text {
    font-size: 1rem;
    max-width: 30rem;
  }
}

.jumbotron p:last-child {
  margin-bottom: 0;
}

.jumbotron p a {
  margin-left: 2px;
}
